
h1
{
  font-size: 20px;
  margin-left: 20px;
  margin-top: 20px;
  font-family: 'Days One', sans-serif ;
  font-weight: lighter;
  text-shadow: 2px 4px 2px rgba(0,0,0,0.6);
  color:rgb(216, 211, 211);
  
}
h1 i{
  font-size: 20px;
  margin-left: 5px;
  color:rgb(216, 211, 211);
 
}

/*    FORM     */
.div-container
{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  width: auto;
  height:60px;
  justify-content: center;
}
.form
{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin: 0px auto;
  height: 50px;
 
}
.contain-input
{
  border: 1px solid #E7E7EB;
  height: 38px;
  border-radius: 5px;
  background-color: rgb(39, 39, 39);
  opacity: 0.6;
}


#nameCity
{
  background-color: transparent;
  outline: none;
  border: none;
  line-height: 38px;
  color: snow;
  font-size: 15px;
}
#nameCity::placeholder
{ color: white;
  font-size: 14px;
}

#icono
{
  color:  #78797a;
  margin-left: 5px;
}

#button-search
{
  width: 70px;
  height: 38px;
  padding: 6px;
  font-size: 14px;
  margin-left: 10px;
  background-color: #50b2e4;
  border: green;
  border-radius: 5px;
  color:snow;
  cursor:pointer;
}

#button-search:hover
{
  background: rgb(45, 188, 245);
}

/*  componente infoweather          */


#today
{
  display: none;
  font-family: "roboto";
  text-align: center;
  margin-bottom: 30px;
  color: rgb(0, 0, 0);
  width: 80%;
  height: auto;
  background-image: linear-gradient(10deg, #a5d9f4 0, #6cb4d8 50%, #1d90bd 100%);
  justify-content: center;
  border: 1px solid #E7E7EB;
  border-radius: 5px;
  opacity:0.9;
  
}

@keyframes fadein{
  0%{opacity: 0 ;}
  100%{opacity:0.9;}
}
.btn-grados
{
  width:20%;
  float:right;
  margin-top: 20px;
}
.celsius
{
  width: 50%;
  float: right;
  background:transparent;
  border: none;
  cursor: pointer;
  margin-bottom: 5px;
  margin-left: 20px; 
  
}

.fahren
{
  float: right;
  width: 50%;
  background:transparent;
  border: none;
  cursor: pointer;
  margin: 0px;
  
}
.clearfix
{
  clear: both;
  float: none;
}
.temp
{
 font-size: 30px;
 font-family: Arial, Helvetica, sans-serif;
 font-weight: bold;
}

.temp-max,
.temp-min{
  width: 100px;
  
  margin-left: 10px;
  height: 30px;
  top:10%;
  text-align:center;
}
.description
{
  text-transform: capitalize;
  
  font-size: 20px;
}
.city
{
  font-size: 20px;
  font-weight: bold;
  margin-top: 20px;
}
.details
{
  display: grid;
  width: 90%;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 15px;
  font-family:"roboto";
  font-size: 14px;
  border-top: 1px solid rgb(54, 53, 53);
  
  padding-top: 15px;
}

.humidity{
  width: 100%;
  
  text-align: end;
}

.windspeed
{
  width: 100%;
  
  text-align: start;
}
.pressure
{
  width: 100%;
  text-align: end;
}

.feelslike
{
  width: 100%;
  text-align: left;
}

#location-icon
{
  width: 100%;
  margin-right: 10px;
}

.week{
  display:grid;
  grid-template-columns: repeat(4, 1fr);
  border-top: 1px solid rgb(53, 53, 53);
  width: 100%;
  height:250px;
}


/*      SOCIAL MEDIA ICONS     */
.social
{
  position: fixed;
  top:40%;
  left: 0px;
  z-index: 2000;
 
}
.social ul{
  list-style: none;
  margin:0px;
  padding: 0px;
}
.social ul a
{
  display: inline-block;
  text-align: center;
  font-size: 20px;
  padding: 10px 15px;
  text-decoration: none;
  color: white;
  transition: all 300ms ease;
  cursor: pointer;
 
}
.social ul li .fa-facebook-f
{
  background-color: RGB(72,103,170);
  width: 20px;
  height: 20px;
}
.social ul li .fa-twitter
{
  background-color: RGB(29,161,242);
  width: 20px;
  height: 20px;
}

.social ul li .fa-instagram
{
  background: RGB(226,48,108);
  width: 20px;
  height: 20px;
}
.social ul li .fa-envelope{
  background-color: rgb(82, 82, 82);
}

.social ul li a:hover{
  padding: 10px 55px;
  background-color: rgb(44, 44, 44);
  border-bottom-right-radius: 25px;
  border-top-right-radius: 25px;
}



/*map*/

#map{
  width: 70%;
  height:200px;
  margin-top: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: #78797a 1px solid;
}

/*             */
@media (max-width:768px){
  .div-container{
    margin-top: 30px;
  }
}


@media (max-width:425px){
  .div-container{
    margin-top: 15px;
    height: 60px;
  }
  .form{
    height: 60px;
  }
  footer{
    font-size: 15px;
  }
}
@media (max-width:360px){
  #today{
    width: 289px;
    margin-top: 20px;
  }
}
@media (max-width:375px){

  #today{
    width: 287px;
    margin-top: 20px;
  }
}

@media (max-width:320px){
  .div-container{
    height: 60px;
  }
  form{
    width: 269px;

    
  }
  #today{
    width: 267px;
    margin-top: 0px;
  }
  .details{
    font-size: 12px;
  }

  #nameCity{
    width: 160px;
  }
  footer{
    font-size: 13px;
  }

  #insta,
  #face,
  #tw{
    font-size: 15px;
  }
}
@media (max-width:375px){

  .form{
   margin-left: 24px;
   margin-right: 24px;
   
  }
}
